.sound-pad {
    cursor: pointer;

    /* Dimensions */
    width: 40vw;
    height: 40vw;
    max-height: 120px;
    max-width: 120px;
    
    /* Margin/Padding */
    margin-bottom: 15px;

    /* Border */
    border-radius: 3px;

    /* Font */
    font-size: 11px;
}

.card {
    background-color: var(--card-bg);
}

.card.sound-pad {
    -webkit-box-shadow: 2px 3px var(--shadow-color);
            box-shadow: 2px 3px var(--shadow-color);
    -webkit-transition: all 0.2s ease;
    -o-transition: all 0.2s ease;
    transition: all 0.2s ease;
}

.card.sound-pad:hover {
    -webkit-filter: brightness(85%);
            filter: brightness(85%);
}

.card.sound-pad:active {
    -webkit-box-shadow: 0px 0px black;
            box-shadow: 0px 0px black;
    -webkit-filter: brightness(60%);
            filter: brightness(60%);
    -webkit-transform: scale(0.99) translateY(3px) translateX(2px);
        -ms-transform: scale(0.99) translateY(3px) translateX(2px);
            transform: scale(0.99) translateY(3px) translateX(2px);
}


